@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 1.6rem;
}

h1 {
  font-size: 6rem;
}

h2{
  font-size: 2.5rem;
}

 .app {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.4);
  color: #fff;
 }

 .header {
  max-width: 95%;
  margin: auto; 
  display: flex;
  justify-content: center;
}

 .app:before {
  content: '';
  background: url('./assets/rainy.png') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1
 }

 .app .search {
  text-align: center;
  padding: 1rem;
 }

 .app input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px; 
  border: 1px solid rgba(255,255,255, 0.8);
  background: rgba(255,255,255, 0.1);
  color: #f8f8f8;
  display: flex;
  margin: auto;
 }

 ::placeholder {
  color: #f8f8f8;
 }

 button {
  background: #f8f8f8;
  border-radius: 5px; 
  border: 1px solid rgba(255,255,255, 0.8);
  margin: 10px;
  padding: 5px;
  color: black;
  font-size: 20px;
}

.button-effects{
  box-shadow: 10px 10px 26px 0px rgba(0, 0, 0, 0.3);
  color: black;
}

 .container {
  max-width: 700px;
  height: 500px; 
  margin: auto; 
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
 }

 .advice {
  margin: auto;
 }

 .app .top {
  width: 100%;
  margin: 1rem auto;
 }

 .app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255,255,255,0.2)
 }

 .bold {
  font-weight: 700;
 }

 .hidden {
  display: none;
  }
  .active {
    display: block;
  }
 /** BEGIN CSS FOR LOADER**/
 .loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
  }

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

#link {color: #E45635;display:block;font: 12px "Helvetica Neue", Helvetica, Arial, sans-serif;text-align:center; text-decoration: none;}
#link:hover {color: #CCCCCC}

#link, #link:hover {-webkit-transition: color 0.5s ease-out;-moz-transition: color 0.5s ease-out;-ms-transition: color 0.5s ease-out;-o-transition: color 0.5s ease-out;transition: color 0.5s ease-out;}


